var _canvasLastCalledTime;
var _canvasFPS;
var _canvasFPSCount = 0;
var _canvasTextColor = 'yellow';
var _canvasTextSize = 32;

export function CanvasFPS_Update() {
    if (!_canvasLastCalledTime) {
        _canvasLastCalledTime = Date.now();
        _canvasFPS = 0;
        _canvasFPSCount = 0;
        return;
    }
    var delta = (Date.now() - _canvasLastCalledTime) / 1000;
    if (delta >= 1) {
        _canvasLastCalledTime = Date.now();
        _canvasFPS = _canvasFPSCount;
        _canvasFPSCount = 0;
    }
    else {
        _canvasFPSCount++;
    }
}

export function CanvasFPS_FontColor(rgb) {
    _canvasTextColor = rgb;
}

export function CanvasFPS_FontSize(size) {
    _canvasTextSize = size;
}

export function CanvasFPS_Render(canvasContext) {
    canvasContext.fillStyle = _canvasTextColor;
    canvasContext.font = _canvasTextSize + "px Arial";
    canvasContext.fillText(_canvasFPS, 10, _canvasTextSize+10);
}