import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import { makeStyles } from '@material-ui/core/styles';
import { SetSubScene, RefreshIntro } from '../actions/scene';

import './FullScreenResult.css';

import Button from '@material-ui/core/Button';
import IconButton from '@material-ui/core/IconButton';
import FaceIcon from '@material-ui/icons/Face';
import PlayArrowIcon from '@material-ui/icons/PlayArrow';
import DeleteIcon from '@material-ui/icons/Delete';
import VideoPlay from './videoPlay'

import { _contents } from './../scene/ContentsData';  

import { GetVideoList, VideoRecordDelete, GetVideoFullList } from './utils/VideoRecord'

const useStyles = makeStyles((theme) => ({
  root: {
    position: 'relative',
    width: '100%',
  },
  contentsTitle_fail: {
    position: 'relative',
    width: '100%',
    left: '0%',
    color: 'red',
  },
  contentsTitle_fail_desc: {
    position: 'relative',
    width: '100%',
    left: '0%',
    color: 'gray'
  },
  video: {
    position: 'relative',
    width: '100%',
    margin: '0',
    //opacity: '0.2'
  },
  divLine: {
    border: "1px solid",
    color: "#DDDDDD",
    width: "95%"
  }
}));

var _contentsTitle = '내기록';
var _recordFailText = '영상 기록 없음';
var _contentsSourceList = undefined;
export const FullScreenResult = ({ _currentSubScene, _refreshIntro, RefreshIntro }) => {
  const classes = useStyles();
  const [_videoFaceMask, setVideoFaceMask] = React.useState(false);

  useEffect(() => {
    _contentsSourceList = GetVideoList(_contents, true);
    if (undefined != _contentsSourceList && 0 < _contentsSourceList.length) {
      //console.log('========FullScreenResult source count:' + _contentsSourceList.length);
    } else {
      //console.log('========FullScreenResult source zero');
    }
  });

  const onToggleVideoFaceMask = (e) => {
    var index = e.target.getAttribute("data-index");
    console.log('onToggleVideoFaceMask call index:'+ index);
    setVideoFaceMask(!_videoFaceMask);
  }

  const onDeleteVideo = (e) => {
    var index = e.target.getAttribute("data-index");
    console.log('onDeleteVideo call index:' + index);
    VideoRecordDelete(parseInt(index));
    //setVideoFaceMask(!_videoFaceMask);
    RefreshIntro();
  }

  const getContentsSummeryText = () => {
    var fullSourceList = GetVideoFullList(_contents);
    var totalCount = fullSourceList.length;
    var validCount = 0;
    for (var i = 0; i < fullSourceList.length; i++) {
      if (true == fullSourceList[i].valid) {
        validCount++;
      }
    }
    return (
      <div>
        <span style={{ color: 'green' }}>{validCount} Set</span>기록 / 총 {totalCount} Set
      </div>
    );
  }

  const getRecordSuccuss = (index, desc, url) => {
    return (
      <div key={desc}>
        <div className='contentsTitle_left'>
          <div className={classes.contentsTitle_success_icon}>
            {/* <img onClick={onToggleVideoFaceMask} data-index={index} className='contentsTitle_success_smile_icon' src={process.env.PUBLIC_URL + '/img/face-mask-icon.png'} /> */}
            <img onClick={onDeleteVideo} data-index={index} className='contentsTitle_success_delete_icon' src={process.env.PUBLIC_URL + '/img/result-scene-delete-icon.png'} />
          </div>
          <span className='contentsTitle_success_text'>{desc}
          </span>
        </div>
        <video className={classes.video} autoPlay={false} controls={true} poster={process.env.PUBLIC_URL + '/img/HomeTrainingQuest_icon.png'}>
          <source src={url} type="video/mp4" id="SelectSoundOutput" />
                Your browser does not support the video tag.
        </video>
      </div>
    )
  }

  const getRecordFail = (desc) => {
    return (
      <div key={desc}>
        <div className='contentsTitle_fail_left' >
          <span className={classes.contentsTitle_fail} >{desc}</span>
        </div>
        <div className='contentsTitle_fail_center' >
          <span className={classes.contentsTitle_fail_desc}>{_recordFailText}</span>
        </div>
      </div>
    )
  }

  const createVideoElement = (index, title, desc, url, isValid) => {
    //console.log('createVideoElement ' + title)
    return (
      <div key={title} >
        {(isValid) ? getRecordSuccuss(index, desc, url) : getRecordFail(desc)}
      </div>);
  }

  const getVideoList = () => {
    let contents = [];
    var fullSourceList = GetVideoFullList(_contents);
    if (undefined != fullSourceList) {
      console.log('fullSourceList:' + fullSourceList.length);
      for (var i = 0; i < fullSourceList.length; i++) {
        contents.push(createVideoElement(fullSourceList[i].step, fullSourceList[i].title, 
          fullSourceList[i].desc, fullSourceList[i].url, fullSourceList[i].valid));
      }
    }
    return contents;
  }


  return (
    <div key='FullScreenResult' className={classes.root}>
      <div className='result_contentsSummery'>
        <span className='result_contentsSummery_left'>{_contentsTitle}</span>
        <span className='result_contentsSummery_right'>{getContentsSummeryText()}</span>
      </div>

      <div className='result_videoRoot'>
          <div className='result_videoContainer'>
            {getVideoList()} 
            <hr className={classes.divLine} />
          </div>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => ({
  _currentSubScene: state.scene._currentSubScene,
  _refreshIntro: state.scene._refreshIntro,
});
const mapDispatchToProps = { SetSubScene, RefreshIntro };

export default connect(mapStateToProps, mapDispatchToProps)(FullScreenResult);
