import React, { useEffect } from 'react';
import queryString from 'query-string';
import { connect } from 'react-redux';
import { setScene } from '../actions/scene';

import { makeStyles } from '@material-ui/core/styles';
import { SetIframeFullScreen, SetLandscapeMode } from './../components/utils/SendMessage';
import FullScreenResult from './../components/FullScreenResult';
import ContentsScene from './ContentsScene';
import VideoBodyScanScene from './VideoBodyScanScene';
import FeedbackScene from './FeedbackScene';
import IntroScene from './IntroScene';
import { _contents } from './ContentsData'
import { GetVideoList } from './../components/utils/VideoRecord';
import './Contents.css'

const useStyles = makeStyles((theme) => ({
    root: {
        width: "100%",
        height: "100%"
    },
}));

export const IndexScene = ({ _currentScene, setScene, location }) => {
    console.log('Index_scene component start');
    const classes = useStyles();
    const [_rewardState, setRewardState] = React.useState(false);
    const [_rewardCategory, setRewardCategory] = React.useState('');
    const [_rewardExp, setRewardExp] = React.useState(0);
    const [_resultSceneVisible, setResultSceneVisible] = React.useState(false);

    useEffect(() => {
        var _contentsSourceList = GetVideoList(_contents, true);
        if (_contentsSourceList.length > 0) {
            setResultSceneVisible(true);
        }

        // return () => {
        //     SetIframeFullScreen(false);
        //     SetLandscapeMode(false);
        // }
    });

    useEffect(() => {
        const query = queryString.parse(location.search);
        console.log(query);
        setRewardState((query.Complete == "true"));
        setRewardCategory(query.reward_category);
        setRewardExp(query.reward_exp);
    }, [location]);

    const getActiveScene = () => {
        switch (_currentScene) {
            case 'Result': {
                SetIframeFullScreen(false);
                SetLandscapeMode(false);
                return <FullScreenResult />;
            }
            case 'Play': {
                SetIframeFullScreen(true);
                SetLandscapeMode(true);
                return <ContentsScene />;
            }
            case 'BodyScan': {
                SetIframeFullScreen(true);
                SetLandscapeMode(true);
                return <VideoBodyScanScene />;
            }
            case 'Feedback': {
                SetIframeFullScreen(false);
                SetLandscapeMode(false);
                return <FeedbackScene resultState={false} />;
            }
            case 'IntroScene': {
                SetIframeFullScreen(false);
                SetLandscapeMode(false);
                var _contentsSourceList = GetVideoList(_contents, true);
                return (
                    <div className='index_contents_root'>
                        {_contentsSourceList.length > 0 ? <FullScreenResult /> : undefined}
                        <IntroScene rewardState={_rewardState} rewardCategory={_rewardCategory} rewardExp={_rewardExp} />
                    </div>
                );
            }
        }
    }

    return (
        <div className={classes.root}>
            {getActiveScene()}
        </div>
    );
}

const mapStateToProps = (state) => ({
    _currentScene: state.scene._currentScene
});
const mapDispatchToProps = { setScene };

export default connect(mapStateToProps, mapDispatchToProps)(IndexScene);