import { SETSCENE, SETSUBSCENE, REFRESHINTRO } from '../actions/scene'

const initialState = {
    _currentScene: 'IntroScene', // 
    _currentSubScene: '',
    _refreshIntro: 0,
};

export default function scene(state = initialState, action) {
    switch(action.type) {
        case SETSCENE:
            console.log('SETSCENE:' + action._changeScene);
            return Object.assign({}, state, {
                _currentScene: action._changeScene
            });
        case SETSUBSCENE:
            console.log('SETSUBSCENE:' + action._changeSubScene);
            return Object.assign({}, state, {
                _currentSubScene: action._changeSubScene
            });
        case REFRESHINTRO:
            console.log('REFRESHINTRO');
            return Object.assign({}, state, {
                _refreshIntro: state._refreshIntro+1
            });
        default:
            return state;
    }
}



