var sleepDuration = 0;
function sleep() {
    const date = Date.now();
    let currentDate = null;
    do {
        currentDate = Date.now();
    } while (currentDate - date < sleepDuration);
}

export function VideoRecord_Start(step) {
    if (undefined == window) {
        return;
    }
    if (undefined == window.HybridApp) {
        return;
    }
    window.HybridApp.StartCameraRecord(step);
    sleep();
    return true;
}

export function VideoRecord_Stop(recoredState, step, title, desc) {
    if (undefined == window) {
        return;
    }
    if (undefined == window.HybridApp) {
        return;
    }
    window.HybridApp.StopCameraRecord();
    sleep();

    // 삭제 처리(임시로 지연 처리)
    if (false== recoredState) {
        setTimeout(() => {
            window.HybridApp.DeleteVideoFile(step);
        }, 1000)
    }
}

export function VideoRecordExist(step) {
    if (undefined == window) {
        return false;
    }
    if (undefined == window.HybridApp) {
        return false;
    }
    return window.HybridApp.IsVideoFileExist(step);
}

export function VideoRecordDelete(step) {
    if (undefined == window) {
        return;
    }
    if (undefined == window.HybridApp) {
        return;
    }
    window.HybridApp.DeleteVideoFile(step);
    sleep();
}

export function GetVideoFullList(contentsData) {
    var list = [];
    for (var i = 1; i < 100; i++) {
        var key = 'step' + i;
        var stepData = contentsData[key];
        if (undefined == stepData) {
            break;
        }
        if (stepData.type != 'set') {
            continue;
        }

        var data = {};
        data.step = i;
        if (window.HybridApp == undefined) {
            data.valid = true;
            data.url = contentsData[key].web_url;
        } else {
            data.valid = window.HybridApp.IsVideoFileExist(i);
            data.url = window.HybridApp.GetVideoFilePath(i);
        }
        data.desc = contentsData[key].desc;
        data.title = contentsData[key].title;
        list.push(data);
    }
    return list;
}

export function GetVideoList(contentsData, recordState) {
    var fullList = GetVideoFullList(contentsData);
    return GetVideoListFromFullList(fullList, recordState);
}

export function GetVideoListFromFullList(fullList, recordState) {
    var list = [];
    fullList.forEach(node => {
        //console.log('=====' + node.title + ":" + node.valid + "/" + node.url);
        if (node.valid == recordState) {
            list.push(node);
        }
    });
    return list;
}

export function SetMediaPlay(url) {
    if (undefined == window) {
        return;
    }
    if (undefined == window.HybridApp) {
        return;
    }
    window.HybridApp.SetMediaPlay(url);
    sleep();
}

