export const config = {
  videoWidth: 900,
  videoHeight: 700,
  flipHorizontal: true,
  algorithm: 'single-pose',
  showVideo: true,
  showSkeleton: true,
  showPoints: true,
  minPoseConfidence: 0.1,
  minPartConfidence: 0.5,
  maxPoseDetections: 2,
  nmsRadius: 20,
  outputStride: 16,
  imageScaleFactor: 0.5,
  skeletonColor: '#ffadea',
  skeletonLineWidth: 6,
  loadingText: 'Loading...please be patient...'
}

function isAndroid() {
  return /Android/i.test(navigator.userAgent);
}

function isiOS() {
  return /iPhone|iPad|iPod/i.test(navigator.userAgent);
}

export function isMobile() {
  return isAndroid() || isiOS();
}

function toTuple({x, y}) {
  return [x, y]
}

export function drawKeyPoints(
  keypoints,
  minConfidence,
  skeletonColor,
  canvasContext,
  scale = 1
) {
  keypoints.forEach(keypoint => {
    if (keypoint.score >= minConfidence) {
      const {x, y} = keypoint.position
      canvasContext.beginPath()
      canvasContext.arc(x * scale, y * scale, pointRadius, 0, 2 * Math.PI)
      canvasContext.fillStyle = skeletonColor
      canvasContext.fill()
    }
  })
}

function drawLine(startX, startY, endX, endY, canvasContext) {
  canvasContext.moveTo(startX, startY)
  canvasContext.lineTo(endX, endY)

  var headlen = 10;
  var dx = endX - startX;
  var dy = endY - startY;
  var angle = Math.atan2(dy, dx);
  canvasContext.moveTo(endX, endY)
  canvasContext.lineTo(endX - headlen * Math.cos(angle - Math.PI / 6), endY - headlen * Math.sin(angle - Math.PI / 6))

  canvasContext.moveTo(endX, endY)
  canvasContext.lineTo(endX - headlen * Math.cos(angle + Math.PI / 6), endY - headlen * Math.sin(angle + Math.PI / 6))
}

function drawSegment(
  [firstX, firstY],
  [nextX, nextY],
  color,
  lineWidth,
  scale,
  canvasContext
) {
  canvasContext.beginPath()

  drawLine(firstX * scale, firstY * scale, nextX * scale, nextY * scale, canvasContext)

  canvasContext.lineWidth = lineWidth
  canvasContext.strokeStyle = color
  canvasContext.stroke()
}

export function drawSkeleton(
  keypoints,
  minConfidence,
  color,
  lineWidth,
  canvasContext,
  scale = 1
) {
  const adjacentKeyPoints = posenet.getAdjacentKeyPoints(
    keypoints,
    minConfidence
  )

  adjacentKeyPoints.forEach(keypoints => {
    drawSegment(
      toTuple(keypoints[0].position),
      toTuple(keypoints[1].position),
      color,
      lineWidth,
      scale,
      canvasContext
    )
  })
}

function drawDegree(
  [firstX, firstY],
  [nextX, nextY],
  color,
  canvasContext
) {

  canvasContext.font = "20px Arial";
  canvasContext.fillStyle = color;
  canvasContext.textAlign = "center";

  var skelectonDir = {};
  skelectonDir.x = nextX - firstX;
  skelectonDir.y = nextY - firstY;

  var current_angle = Posenet_GetDegreeFormVec(skelectonDir);

  var posX = (firstX + nextX) / 2;
  var posY = (firstY + nextY) / 2;
  canvasContext.fillText("(" + current_angle + ")", posX, posY);

  // canvasContext.beginPath()
  // canvasContext.moveTo(firstX * scale, firstY * scale)
  // canvasContext.lineTo(nextX * scale, nextY * scale)
  // canvasContext.lineWidth = lineWidth
  // canvasContext.strokeStyle = color
  // canvasContext.stroke()
}

export function drawSkeletonDegree(
  keypoints,
  minConfidence,
  color,
  lineWidth,
  canvasContext,
  scale = 1
) {
  const adjacentKeyPoints = posenet.getAdjacentKeyPoints(
    keypoints,
    minConfidence
  )

  adjacentKeyPoints.forEach(keypoints => {
    drawDegree(
      toTuple(keypoints[0].position),
      toTuple(keypoints[1].position),
      color,
      lineWidth,
      scale,
      canvasContext
    )
  })
}
