import React, { useEffect } from 'react';
import { connect } from 'react-redux';

import { setScene } from '../actions/scene';
import { InvalidPosenetClear } from '../actions/posenet';

import ResultFailure from '../components/ResultFailure';
import ResultSuccess from '../components/ResultSuccess';

import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';

import VideoPlay from '../components/videoPlay'
import RewardButton from './../components/RewardButton';
import Button from '@material-ui/core/Button';
import { _contents } from './ContentsData'
import { GetIframeReward, SetIframeFinish } from './../components/utils/SendMessage';
import './Contents.css';
import { GetVideoListFromFullList, GetVideoFullList} from './../components/utils/VideoRecord'

const useStyles = makeStyles((theme) => ({
    root: {
        position: 'relative',
        width: '100%',
    },
    videoContainer: {
        width: '100%',
        position: 'relative',
    },
    contentsTitle: {
        color: 'black'
    },
    BottomBarBack: {
        position: "fixed",
        backgroundColor: '#FFFFFF88',
        width: '100%',
        height: '11%',
        top: '89%',
        left: '0%'
    },
    BottomBarGroup: {
        position: "fixed",
        bottom: "70px",
        width: "100%"
    },

    BottomBarButton: {
        position: "absolute",
        height: "85%",
        top: "5%",
        background: 'linear-gradient(90deg, #cc6699 30%, #6666ff 80%)',
        borderRadius: 3,
        border: 0,
        color: 'white',
        boxShadow: '0 3px 5px 2px rgba(0, 0, 0, .3)',
        width: "94%",
        left: "3%",
        height: "50px",
        top: "5px"
    },
    BottomBarButton_text_center: {
        position: 'absolute',
        left: "0",
        top: "5%",
        width: "100%",
        fontSize: '30px',
        fontWeight: 'bold'
    },
    BottomBarButton_text_1: {
        position: 'absolute',
        left: "0",
        top: "5%",
        width: "100%",
        fontSize: '20px',
        fontWeight: 'bold'
    },
    BottomBarButton_text_2: {
        position: 'absolute',
        left: "0",
        top: "60%",
        width: "100%",
        fontSize: '12px',
    }
}));


export const IntroScene = ({ _posenetPose, setScene, _refreshIntro, 
    rewardState, rewardCategory, rewardExp }) => {
    const classes = useStyles();
    console.log('IntroScene start');

    const createVideoElement = (title, desc, url) => {
        //console.log('createVideoElement ' + title)
        var poster = url.replace(".mp4", ".jpg");
        return (
            <div key={title} >
                <span className={classes.contentsTitle}>{desc}</span>
                <div className={classes.videoContainer}>
                    <VideoPlay key={url} title={title} enableContentsSkip={false} enableContentsPause={true} videoUrl={url} autoPlay={false} enableTitleText={false} enableFullScreen={false} enableScreenMirror={false} poster={poster} />
                </div>
            </div>);
    }

    const getContents = () => {
        let contents = [];
        Object.keys(_contents).forEach(function (k, v) {
            if (_contents[k].type == 'set') {
                var url = _contents[k].url;
                if (window.HybridApp == undefined) {
                  url = _contents[k].web_url;
                }
                contents.push(createVideoElement(_contents[k].title, _contents[k].desc, url));
            }
        });
        return contents;
    }

    const onPlay = () => {
        console.log('onPlay call');
        setScene('Play');
        //setScene('BodyScan');
    }

    const onGetRewardClick = () => {
        console.log("onGetReward call");
        GetIframeReward();
    }

    const onFinishClick = () => {
        console.log("onFinishClick call");
        SetIframeFinish();
    }

    const getMainButton = () => {
        var videoFullList = GetVideoFullList(_contents);
        var invalidCount = GetVideoListFromFullList(videoFullList, false);
        var validCount = GetVideoListFromFullList(videoFullList, true);

        if (true == rewardState) {
            return (<RewardButton onClick={onGetRewardClick} rewardCategory={rewardCategory} rewardExp={rewardExp} ></RewardButton>);
        } else {
            if (invalidCount == 0) {
                return (
                    <Button onClick={onFinishClick} className={classes.BottomBarButton} variant="contained" color="secondary">
                        <span className={classes.BottomBarButton_text_center}>기록 제출하기</span>
                    </Button>
                );
            } else if (validCount > 0) {
                return (
                    <Button onClick={onPlay} className={classes.BottomBarButton} variant="contained" color="secondary">
                        <span className={classes.BottomBarButton_text_center}>재도전하기</span>
                    </Button>
                );
            }            
            return (
                <Button onClick={onPlay} className={classes.BottomBarButton} variant="contained" color="secondary">
                    <span className={classes.BottomBarButton_text_1}>영상 기록 시작</span>
                    <span className={classes.BottomBarButton_text_2}>총 11 Set / 15 분</span>
                </Button>
            );
        }
    }

    return (
        <div className={classes.root}>
            <div className='contents_body'>
                <div className='contents_intro'>
                    <span className='.contents_intro_text'>어디스든 가볍게!</span>
                    <span className='contents_intro_text_sub'><br />딱 16분 트레이닝으로 코어를 단단하게 조이고, 복근을 활성화 합니다.<br />따라오세요</span>
                </div>
                {getContents()}
                <br />
                <br />
                <br />
                <div className={classes.BottomBarBack}></div>
            </div>
            <div className={classes.BottomBarGroup}>
                {getMainButton()}
            </div>
        </div>
    );
}

const mapStateToProps = (state) => ({
    _posenetPose: state.posenet._posenetPose,
    _refreshIntro: state.scene._refreshIntro
});
const mapDispatchToProps = { setScene };

export default connect(mapStateToProps, mapDispatchToProps)(IntroScene);
