
export function Posenet_GetDegreeFormVec(vec) {
    var pivotVec = {};
    pivotVec.x = 0;
    pivotVec.y = -1;

    var cos = get_cos(pivotVec, vec);
    var degree = parseInt(Math.acos(cos) * (180 / Math.PI));
    if (vec.x < 0) {
        degree += 180;
    }
    return degree;
}

export function Posenet_GetDegreeFormVec2(vec1, vec2) {
    var cos = get_cos(vec1, vec2);
    var degree = parseInt(Math.acos(cos) * (180 / Math.PI));
    return degree;  
}

export function InBoundaryDegree(targetDegree, currentDegree, boundaryDegree) {
    var diffDegree = Math.abs(currentDegree - targetDegree);
    if (diffDegree <= boundaryDegree) {
        return true;
    }
    return false;
}

function abs_value(vec) {
    var distance = Math.pow(vec.x - 0, 2) + Math.pow(vec.y - 0, 2);
    return Math.sqrt(distance);
}

function get_cos(partA, partB) {
    let dotProduct = ((partA.x * partB.x) + (partA.y * partB.y));
    let cosineSimilarity = dotProduct / (abs_value(partA) * abs_value(partB));
    return cosineSimilarity;
}